.button,
[class*="button--"] {
  display: inline-block;
  padding: .8em 1.6em;
  background: #007BDF;
  color: #FFF;
  margin: 0 2rem 0 0;
  cursor: pointer;
  text-decoration: none;
  border-radius: .25rem;
  transition: transform .05s;
  border: none;
  text-align: center;
}

.button:hover,
[class*="button--"]:hover {
  background: #006dc6;
  text-decoration: inherit;
}

.button:active,
[class*="button--"]:active {
  transform: scale(0.99);
}

.button.center,
[class*="button--"].center {
  display: table;
  margin: 0 auto;
}

.button.full,
[class*="button--"].full {
  margin: 0;
  width: 100%;
}

.button.button--small,
[class*="button--"].button--small {
  padding: .3em .8em;
}

.button--secondary {
  background: #00CBFF;
}

.button--secondary:hover {
  background: #00c3f5;
}

.button--tertiary {
  background: #FF3C32;
}

.button--tertiary:hover {
  background: #ff1f13;
}

.button--cta {
  background: #FBA905;
}

.button--cta:hover {
  background: #f2a304;
}

.button--dark {
  background: #171A1C;
}

.button--dark:hover {
  background: black;
}

[class*="button--ghost"] {
  color: #007BDF;
  background: none;
  border: 1px solid;
}

[class*="button--ghost"]:hover {
  background: #007BDF;
  color: #FFF;
  border-color: transparent;
}

.button--ghost--secondary {
  color: #00CBFF;
}

.button--ghost--secondary:hover {
  background: #00CBFF;
  color: #FFF;
}

.button--ghost--tertiary {
  color: #FF3C32;
}

.button--ghost--tertiary:hover {
  background: #FF3C32;
  color: #FFF;
}

.button--ghost--cta {
  color: #FBA905;
}

.button--ghost--cta:hover {
  background: #FBA905;
  color: #FFF;
}

.button--ghost--dark {
  color: #171A1C;
}

.button--ghost--dark:hover {
