/*
  MAIN MENU
  Este es el menú principal del sitio, aparece dentro de main header

  Su estructura es
    <nav class="main-menu" id="main-menu">
      <ul>
        <li><a href="#">Item de menu</a></li>
        <li><a href="#">Item de menu</a></li>
      </ul>
    </nav>

  En movil se activa con un botón de hamburguesa.
  En el ejemplo es un div pero puede ser cualquier elemento
  (debe estar fuera del nav)
    <div id="main-menu-toggle" class="main-menu-toggle to-l"></div>

  Entra por la derecha justo debajo de main-header

*/
body {
  position: relative;
  right: 0;
  transition: right .5s;
}

@media screen and (max-width: 1023px) {
  body.main-menu-visible {
    right: 80%;
    overflow-y: hidden;
  }
}

.main-menu-toggle {
  position: relative;
  z-index: 300;
  width: 30px;
  height: 20px;
  box-shadow: 0 4px #53575A inset;
  cursor: pointer;
}

.main-menu-toggle::after, .main-menu-toggle::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 4px;
  background-color: #53575A;
  bottom: 0;
}

.main-menu-toggle::after {
  bottom: 8px;
}

.main-menu {
  font-size: 1rem;
}

@media screen and (max-width: 1023px) {
  .main-menu {
    position: fixed;
    top: 64px;
    background: rgba(0, 123, 223, 0.95);
    width: 80%;
    right: -80%;
    padding: 1em 1em 2em;
    height: calc(100vh - 64px);
    transition: right .5s;
    overflow-y: auto;
  }
  .main-menu.show {
    right: 0;
  }
}

.main-menu ul {
  list-style: none;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  margin-left: 0;
}

@media screen and (min-width: 1024px) {
  .main-menu ul {
    display: flex;
  }
}

.main-menu li {
  color: #FFF;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  padding: 1em 0;
}

@media screen and (min-width: 1024px) {
  .main-menu li {
    color: #53575A;
    padding: 0;
    margin-left: 2rem;
  }
}

.main-menu a {
  color: inherit;
}

.vertical-menu-toggle {
  background-size: 100% 34%;
  font-size: 1rem;
}

@media screen and (max-width: 1023px) {
  .vertical-menu-toggle {
    display: flex;
  }
}

.vertical-menu-toggle::before {
  content: "";
  display: block;
  width: .5em;
  height: 1.2em;
  background-image: radial-gradient(circle, #53575A 2px, transparent 2px);
  background-size: 100% 34%;
  margin-right: .2em;
}

.vertical-menu-toggle::after {
  content: attr(data-content);
}

@media screen and (max-width: 1023px) {
  .vertical-menu {
    max-height: 0;
    transition: max-height .5s;
    overflow: hidden;
  }
  .vertical-menu.show {
    max-height: 1000px;
    margin-bottom: 1em;
  }
}

.vertical-menu ul {
  list-style: none;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  margin-left: 0;
}

.vertical-menu li {
  padding: 1em 0;
  border-bottom: 1px solid #DFE0E0;
  font-size: 1rem;
  color: #53575A;
}

.vertical-menu a {
  color: inherit;
  display: block;
}

.vertical-menu a.active {
  display: flex;
  justify-content: space-between;
